<template>
  <div>
    <el-table
        :data="tableData"
        border
        style="width: 100%;"
        :header-cell-style="headClass"
        :cell-style="rowClass">
      <el-table-column
          prop="certId"
          label="id" style="text-align: center">
      </el-table-column>
      <el-table-column
          prop="certName"
          label="" >
      </el-table-column>
      <el-table-column
          prop="p12Password"
          label="">
      </el-table-column>
      <el-table-column
          prop="certStatus"
          label="">
        <template slot-scope="scope">
          <div>{{scope.row.certStatus == 1?"":""}}</div>
          
        </template>
      </el-table-column>
      <el-table-column
          prop="remark"
          label="">
      </el-table-column>
      <el-table-column
          prop="endTime"
          label="">
      </el-table-column>
      <el-table-column
          label="" >
        <template slot-scope="scope">
          <el-button  @click="handleClick(scope.row.certId)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        style="text-align: center;margin-top: 20px"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5, 40, 80, 100]"
        :page-size="count"
        layout="total, sizes, prev, pager, next, jumper"
        :page-count="pages"
        :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  created() {
    this.update(this.page,this.count);
  },
  methods: {
    handleSizeChange(val) {
      console.log("" + val)
      this.count = val
      this.update(this.page,this.count);
    },
    handleCurrentChange(val) {
      this.page = val
      this.update(this.page,this.count);
      console.log(`: ${val}`);
    },
    handleClick(row) {
      var that = this
      this.$http.post('/admin/deleteMdmCert?certId=' + row)
          .then(function (response) {
            that.$load(0)
            if(response.data.code == 0){
              that.update(that.page,that.count);
            }else {
              that.$message.error(response.data.message)
            }
          })
          .catch(function (error) {
            that.$load(0)
            that.$message,error("")
          });
    },
    update(pageNum,pageSize){
      var that = this
      that.$load(1,"")
      this.$http.get('/admin/queryMdmCert?pageNum=' + pageNum + '&pageSize=' + pageSize)
          .then(function (response) {
            that.$load(0)
            if(response.data.code == 0){
              console.log(response.data.data)
              that.tableData = []
              for (let res in response.data.data){
                var temp = response.data.data[res]
                if(temp.status === 1){
                  temp.status = ""
                }else {
                  temp.status = ""
                }
                that.tableData.push(temp)
              }
              that.pages = response.data.pages
              that.total = response.data.total
            }else {
              that.$message.error(response.data.message)
            }
          })
          .catch(function (error) {
            that.$load(0)
            that.$message,error("")
          });
    },
    headClass() { //
      return "text-align:center"
    },
    rowClass() { //
      return "text-align:center"
    }
  },
  data() {
    return {
      tableData: [],
      count: 5,
      page: 1,
      pages: 0,
      total: 1
    }
  }
}
</script>

<style lang="less" >
.el-pagination{
  .el-input__inner{
    height: 30px;
  }
}
</style>
